/* Color Theme Swatches in Hex */
:root {
  --white: #fafafa;
  --dark-blue: #044BD9;
  --blue: #3084F2;
  --light-blue: #4B94F2;
  --sky: #91BBF2;
  --gray: #e2e1e1;
  --dark-gray: #a9a9a9;
  --black: #474747;
}




.sidebar{
    flex: 1;
    border-right: 1px solid var(--gray);
    min-height: 100vh;
    box-shadow:  0px 0px 4px 0px rgba(0, 0, 0, 0.123);
    background: var(--white);
   
    .top{
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-bottom: 1px solid var(--gray);

        
        .logo{
            font-size: 20px;
            font-weight: bold;
            color: var(--dark-blue);
            text-transform: uppercase;
        }
    }
    .center{
        padding-left: 10px;

        ul{
            list-style: none;
            padding: 0;
            margin: 0;
            margin-top: 10px;

           .title{
            text-transform: uppercase;
            font-size: 10px;
            font-weight: bold;
            color: var(--dark-gray);
            margin-bottom: 5px;
            margin-top: 15px;
           }
           li{
            display: flex;
            align-items: center;
            padding: 5px;
            cursor: pointer;

            &:hover{
                background: var(--sky);
                color: white;
            }
            .icon{
                color: var(--dark-blue);
                font-size: 18px;
            }
            span{
                font-size: 13px;
                font-weight: 600;
                color: var(--black);
                margin-left: 10px;
            }
           }
        }
    }
    .bottom{
        display: flex;
        align-items: center;
        margin: 20px;

        .colorOption{
            width: 20px;
            height: 20px;
            // border-radius: 50%;
            border: 1px solid var(--gray);
            margin-right: 10px;
            cursor: pointer;

            &:nth-child(1){
                background: var(--white);
            }
            &:nth-child(2){
                background: var(--black);
            }
            &:nth-child(3){
                background: var(--dark-blue);
            }
        }
    }
}